/** Time (ms) window to not display an ad if it's about to expire. */var AD_EXPIRY_THRESHOLD=5*60*1000;/** Whether the ad is expired or about to expire. */var isExpired=function isExpired(ad){var threshold=arguments.length>1&&arguments[1]!==undefined?arguments[1]:AD_EXPIRY_THRESHOLD;if(ad.expires_at){var now=new Date();return now.getTime()>new Date(ad.expires_at).getTime()-threshold;}else{return false;}};export{isExpired};